@import "../../../../theme/colors.css";

.container {
  width: 100%;
  position: relative;
}

.map {
  height: 460px;
}

.load {
  position: absolute;
  z-index: 401;
  width: 100%;
  height: 100%;
  background-color: grey;
  opacity: 0.3;
}

.errors {
  position: absolute;
  top: 0;
  z-index: 402;
  text-align: center;
  width: 100%;
  background-color: var(--red);
}

@media (max-width: 768px) {
  .container {
    width: 100%;
  }
}



